header
    display: flex
    justify-content: space-between
    align-items: center
    height: 80px
    @include for-size(max-mobile)
      flex-direction: column
      justify-content: center
      gap: 15px
      height: 100px 
    ul
        flex-basis: 20%
        display: flex
        justify-content: space-between 
        @include for-size(max-mobile)
          width: 100% 
        @include for-size(tablet-landscape-up )
           flex-basis: 50%


        li
            a
                color: $White
                &:hover
                    cursor: pointer 
                    text-decoration: underline
.partTowMine
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  gap: 20px
  padding: 20Px 60Px
  img
    width: 400px
    @include for-size(mobile-small)
       width: 300px
  h1,p
    color: $White
    max-width: 600px
    text-align: center
  p
    font-size: 14px
  



                    
